<template>
  <div>
    <div class="shadow p-3 mb-5 bg-white rounded text-left">
      <p class="text-info">
        Bir süredir Vue js ile ilgili sadece kodlayarak video çekip yüklüyorum.
        <a
          href="https://www.youtube.com/channel/UC-XhAb5WL2LzA8c46138TDQ"
          target="_blank"
        >
          Buradan onlara ulaşabilirsiniz.</a
        >
      </p>
    </div>
    <ul>
      <li :key="index" v-for="(item, index) in items.items">
        <div
          class="card text-white bg-info mb-3"
          style="width: 400px; margin: 0px 10px; padding-right: 15px"
        >
          <iframe
            width="400"
            height="400"
            :src="getUrl(item.snippet.resourceId.videoId)"
            frameborder="0"
            allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
            allowfullscreen
          ></iframe>
          <div class="card-body">
            <h5 class="card-title">{{ item.snippet.title }}</h5>
            <p class="card-text">{{ item.snippet.description }}</p>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "MyVideo",
  data() {
    return {
      items: [],
    };
  },
  created() {
    fetch(
      "https://www.googleapis.com/youtube/v3/playlistItems?part=snippet%2CcontentDetails&maxResults=50&playlistId=PLDG4qXSgXzW5vFHkq6H2dJeTgHGxh8tVd&key=AIzaSyDqg9vDShgSwqtmDKeEaYcpSlCUTx2VWrg"
    )
      .then((res) => {
        return res.json();
      })
      .then((videos) => {
        this.items = videos;
      });
  },

  methods: {
    getUrl(videoid) {
      var youtubeEmbed = "https://www.youtube.com/embed/";
      return youtubeEmbed + videoid;
    },
  },
};
</script>

<style scoped>
li {
  display: inline-block;

  width: 30%;
}
</style>
